                  default:
                    //alert("Something went wrong with the assets");
                    break;
                }
              })
              .catch((err) => {
                console.error('Login failed.'+err);
              })
          },
          getAssets() {
            console.log(this.borrower.student_id);
            const options = {
              url: 'https://dca.durhamcollege.ca/~signout/services/get_assets.php',
              method: 'POST',
              data: {
                student_id: this.student_id
              }
            }
            this.$axios(options)
              .then((res) => {
                console.log(res.data);
                //this.borrowers = res.data;
                switch (res.data.error.id) {
                  case 0 :
                    // ? push down to home
                    this.categories = res.data.categories;
                    console.log("categories: ");
                    console.log(this.categories);
                    break;
                /* add other responses here */
                  default:
                    //alert("Something went wrong with the assets");
                    break;
                }
              })
              .catch((err) => {
                console.error('Login failed.'+err);
              })
          },

          getLogIn(e) {
            console.log(e.student_id);
            //this.student_id = e.student_id;
            const options = {
              url: 'https://dca.durhamcollege.ca/~signout/services/login.php',
              method: 'POST',
              data: {
                student_id: e.student_id,
                password: e.password
              }
            }
            this.$axios(options)
              .then((res) => {
                //console.log(res.data.borrower.borrower_id);
